import clsx from 'clsx';

// To use the Rokets logo from Figma:
// 1. Export the logo from Figma as SVG (recommended) or PNG
// 2. Place the file in the commerce/public folder as "rokets-logo.svg" or "rokets-logo.png"
// 3. Set USE_FIGMA_LOGO to true below
// 
// How to export from Figma:
// - Select the logo frame/component
// - Right-click > Export > Select SVG or PNG
// - Save as "rokets-logo.svg" (or .png) in commerce/public folder

const USE_FIGMA_LOGO = false; // Set to true when logo file is added to public folder

export default function LogoIcon(props: React.ComponentProps<'svg'>) {
  // Use external logo file from Figma if available
  if (USE_FIGMA_LOGO) {
    const logoPath = '/rokets-logo.svg';
    const width = props.width || 24;
    const height = props.height || 24;
    
    return (
      <img
        src={logoPath}
        alt={`${process.env.SITE_NAME || 'Rokets'} logo`}
        width={width}
        height={height}
        className={clsx('object-contain', props.className)}
        style={{ width, height }}
      />
    );
  }

  // Inline SVG fallback (current logo)
  return (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      aria-label={`${process.env.SITE_NAME} logo`}
      viewBox="0 0 24 28"
      {...props}
      className={clsx('h-4 w-4 fill-black dark:fill-white', props.className)}
    >
      {/* Rocket body */}
      <path d="M12 2L8 10L8 18L12 24L16 18L16 10L12 2Z" />
      {/* Rocket nose */}
      <path d="M12 2L10 6L12 10L14 6L12 2Z" />
      {/* Rocket fins */}
      <path d="M8 10L4 14L6 16L8 14L8 10Z" />
      <path d="M16 10L16 14L18 16L20 14L16 10Z" />
      {/* Rocket window */}
      <circle cx="12" cy="14" r="1.5" fill="white" className="dark:fill-black" />
      {/* Rocket flame */}
      <path d="M10 18L12 22L14 18L12 20L10 18Z" />
    </svg>
  );
}
